import * as React from "react";
import type { HeadFC, PageProps } from "gatsby";
import Header from "../components/header";
import Weather from "../components/weather";

const response = await fetch(
  "https://api.open-meteo.com/v1/forecast?latitude=52.52002&longitude=13.41&current=temperature_2m,is_day&wind_speed_unit=ms&timezone=auto&forecast_days=1"
);
const data = await response.json();

const weather = {
  temp: data.current.temperature_2m,
  isDay: data.current.is_day ? true : false,
};

const badgeStyle = {
  color: "#fff",
  backgroundColor: "#088413",
  border: "1px solid #088413",
  fontSize: 11,
  fontWeight: "bold",
  letterSpacing: 1,
  borderRadius: 4,
  padding: "4px 6px",
  display: "inline-block",
  position: "relative" as "relative",
  top: -2,
  marginLeft: 10,
  lineHeight: 1,
};

const IndexPage: React.FC<PageProps> = () => {
  return (
    <main className="bg-[conic-gradient(at_top,_var(--tw-gradient-stops))] from-blue-100 via-blue-200 to-blue-400 w-screen h-screen p-12 *:font-mono flex justify-center items-center">
      <div
        role="definition"
        className="p-3 rounded-lg bg-white shadow-md shadow-slate-400"
      >
        <Header />
        <Weather />
      </div>
    </main>
  );
};

export default IndexPage;

export const Head: HeadFC = () => <title>Home Page</title>;
