import React from "react";

const response = await fetch(
  "https://api.open-meteo.com/v1/forecast?latitude=52.52002&longitude=13.41&current=temperature_2m,is_day&wind_speed_unit=ms&timezone=auto&forecast_days=1"
);
const data = await response.json();

const weather = {
  temp: data.current.temperature_2m,
  isDay: data.current.is_day ? true : false,
};

function Weather() {
  return (
    <div className="py-5 px-6 border-dashed border-black border rounded-x-md rounded-b-md">
      <h2 className="font-semibold text-slate-700 text-lg mt-3">
        Today's temperature is...
      </h2>
      <h1 className="font-bold  text-3xl mt-2 mb-11">{weather.temp}C</h1>
      {weather.isDay ? (
        <p className="font-semibold text-orange-800 text-lg">
          It's sunny outside
        </p>
      ) : (
        <p className="font-semibold text-blue-800 text-lg">It's nighttime</p>
      )}
    </div>
  );
}

export default Weather;
